@use 'base/variables' as v;
@use 'base/mixins' as m;

.galery {
    padding: 5rem 0;

    h3 {
        text-align: center;
    }
}

.img-galery {
    @include m.resetear-lista;
    @include m.grid(2, 2rem);

    @include m.phone {
        @include m.grid(3, 2rem);
    }
    @include m.tablet{
        @include m.grid(4, 2rem);
    }
    @include m.desktop {
        @include m.grid(6, 2rem);
    }
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    width: 100%;
    height: 100%;

    display: grid;
    place-items: center;

    background-color: rgba(0,0,0,.5);
    picture {
        display: block;
        min-width: 80rem;
    }
}

.close-btn {
    width: 5rem;
    height: 5rem;
    background-color: v.$white;
    border-radius: 50%;
    color: v.$black;
    display: grid;
    place-items: center;
    font-weight: 700;

    &:hover {
        cursor: pointer;
    }
}