@mixin phone {
    @media (min-width: v.$phone){
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet){
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop){
        @content;
    }
}
@mixin desktopxl {
    @media (min-width: v.$desktopxl){
        @content;
    }
}

@mixin grid ($columns, $space) {
    display: grid;
    grid-template-columns: repeat($columns, 1fr);
    gap: $space;
}

@mixin resetear-lista {
    margin: 0;
    padding: 0;
    list-style: none;
}