@use 'base/variables' as v;
@use 'base/mixins' as m;


.video {
    position: relative;
    max-height: 70rem;
    min-height: 50rem;
    overflow: hidden;

    .overlay {
        position: absolute;
        height: 100%;
        width: 100%;
        background: rgb(149, 15, 218);
        background: linear-gradient(270deg, rgba(149, 15, 218, 0.49761911600577735) 0%, rgba(255, 0, 157, 0.2959384437368697) 100%);
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    video {
        width: auto;
        display: block;
        @include m.desktop {
            width: 100%;
        }
    }
}

.video-container {
    h2 {
        color: v.$white;
        font-size: 7rem;
        margin: 0;
    }

    p {
        background-color: v.$pink;
        display: inline-block;
        padding: 1rem 2rem;
        color: v.$white;
        font-weight: 700;
        font-size: 2.4rem;
        margin: 0;
    }
}